import React, { Fragment } from "react";
import Footer from "./../../layouts/common/Footer/Footer";
import NavBar from "./../../layouts/common/NavBar/NavBar";
import RedesSociais from "./../../layouts/common/RedesSociais/RedesSociais";
import MapaSite from "./../../layouts/common/MapaSite/MapaSite";
import Breadcrumb from "./../../layouts/common/Breadcrumb/Breadcrumb";
import { Link } from "react-router-dom";
import PageComponent from "./../../core/PageComponent";
import OnlineAdvertisingSVG from './OnlineAdvertisingSVG';

export default class OnlineAdvertising extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside facebook-ads">
            
              <NavBar />
            

            <main>
              <section id="wrapper-banner">
                <div id="banner-title">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <h1>
                          <strong>Publicidade Online</strong>
                        </h1>
                      </div>
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-master">
                <Breadcrumb>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <span>Publicidade Online</span>
                  </li>
                </Breadcrumb>

                {/* <div className="master">
                  <div className="container">
                    <div className="row">
                      <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                        <div className="wrapper-content">
                          <h2>O que  o Facebook Ads?</h2>
                          <p>
                            O Facebook Ads  a ferramenta que exibe os anncios
                            dentro do Facebook. Nele  possvel patrocinar seus
                            posts para que atinjam uma parcela de pessoas que
                            no seguem sua empresa na rede social, mas que tem
                            caractersticas semelhantes a sua persona de
                            cliente, ou seja,  possvel segmentar cada anncio
                            para aumentar sua assertividade.
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div> */}

                <div className="container">
                  <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <span className="icon consultoria-seo">
                          <OnlineAdvertisingSVG />
                        </span>
                      </div>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                      <div className="wrapper-content">
                        <p>
                          Invista em propagandas online! Gere resultados
                          expressivos em menos tempo
                        </p>

                        <p>
                          Compre palavras-chave no Google Adwords Sua empresa
                          acima das concorrentes quando seu consumidor pesquisar
                          por produtos ou informaes.
                        </p>

                        <p>
                          Patrocine posts nas redes sociais Para que aquela
                          pessoa que ainda no te segue - mas faz parte do seu
                          pblico-alvo  veja a sua postagem e se interesse.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="section">
                  <div className="container">
                    <div className="row">
                    </div>
                  </div>
                </div>
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>

              <footer id="wrapper-footer">
                <Footer />
              </footer>
            </main>
          </div>
        </div>
      </Fragment>
    );
  }
}



